.preloaderWrapper {
   position: fixed;
   width: 100%;
   height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #fff;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 2;
}
.preloader {
   display: block;
   width: 100px;
}
